export class UploadFile {
  file: File;//
  name: string;//name
  fileName: string;//
  fileExtension: string;//
  fileType: string;//
  fileSize: number;//
  dataUrl: string;//base64
  compressedDataUrl: string;//

  valid:boolean;//

  compressed: boolean;//

  progress: number;//
  uploaded: boolean;//
  success: boolean;//
  error: boolean;//
  response: any;//

  customData: any;//
  submitData: {name: string,value: any}[];//

  xhr:XMLHttpRequest;//XMLHttpRequest
  constructor(){
    this.compressed=false;
    this.progress=0;
    this.uploaded=false;
    this.success=false;
    this.error=false;
    this.response=null;
    this.submitData=[];
    this.customData={};//
  }

  setSuccess() {
    this.success = true;
    this.error = false;
  }

  setError() {
    this.success = false;
    this.error = true;
  }

  /**
   * ()
   * @param type 0:Filed/Blob 1:Base64
   */
  getFile(type?:number):any{
    let result;
    if(type===1){
      if(this.compressed){
        result=this.compressedDataUrl;
      }else{
        result=this.dataUrl;
      }
    }else{//0
      if(this.compressed){
        result=this.createBlob(this.compressedDataUrl);
      }else{
        result=this.file;
      }
    }
    return result;
  }

  addSubmitData(name:string,value:any){
    if(!(this.submitData instanceof Array)){
      this.submitData=[];
    }
    this.submitData.push({
      name:name,
      value:value
    });
  }
  /**
   * Blob
   * @param dataUrl
   */
  private createBlob(dataUrl: string): Blob {
    let arr = dataUrl.split(',');
    let mime = arr[0].match(/:(.*?);/)[1];
    let bstr = atob(arr[1].replace(/\s/g, ''));
    let n = bstr.length;
    let u8arr = new Uint8Array(n);
    while (n--) {
      u8arr[n] = bstr.charCodeAt(n);
    }
    return new Blob([u8arr], {type: mime});//
  }
}



